export function fileNameFromPath(path: string): string {
  return path.split(/\/|\\/).pop() ?? '';
}

export function fileNameFromPathParts(parts: string[]): string {
  if (parts.length === 0) return '.';
  return parts[parts.length - 1];
}

export function bytes2String(bytes: number): string {
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
  if (bytes === 0) return '0 Byte';
  const i = Math.floor(Math.log(bytes) / Math.log(1024));
  return `${(bytes / Math.pow(1024, i)).toFixed(2)} ${sizes[i]}`;
}

export function string2bytes(str: string): number {
  const units = ['B', 'KB', 'MB', 'GB', 'TB'];
  const match = str.match(/^(\d+(\.\d+)?)\s*(\w+)$/);
  if (!match) return 0;
  const size = parseFloat(match[1]);
  const unit = match[3];
  const index = units.indexOf(unit);
  if (index === -1) return 0;
  return size * Math.pow(1024, index);
}

export function isImage(path: string): boolean {
  return ['.png', '.svg', '.jpg', '.jpeg', '.ico', '.webp'].some((ext) => path.endsWith(ext));
}
