<template>
  <div :style="extendedOuterStyle" :class="outerClass">
    <div :style="extendedInnerStyle" :class="innerClass">
      <slot></slot>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed, CSSProperties, defineProps } from 'vue';

const props = defineProps<{
  innerClass?: string;
  outerClass?: string;
  outerStyle?: CSSProperties;
  innerStyle?: CSSProperties;
}>();
const extendedOuterStyle = computed<CSSProperties>(() =>
  props.outerStyle
    ? {
        position: 'relative',
        ...props.outerStyle,
      }
    : {
        position: 'relative',
      },
);

const extendedInnerStyle = computed<CSSProperties>(() => {
  return {
    position: 'absolute',
    inset: 0,
    overflow: 'auto',
    ...(props.innerStyle ?? {}),
  };
});
</script>
